<template>
    <el-container>
      <el-aside width='auto'>
         <ce/>
      </el-aside>
      <el-container>
        <el-header>
          <hearder/>
        </el-header>
        <tagger/>
        <el-main>
            <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
</template>
<script>
import ce from '../src/components/ce.vue'
import hearder from '../src/components/hearder.vue'
import tagger from '../src/components/tagger.vue'
import{ getlist }from '../api/data'
import Cookie from 'js-cookie'
export default {
  name: 'shouye',
  components:{
      ce,
      hearder,
      tagger
  },
  mounted() {
    const cc = {token:Cookie.get('token')}
    console.log(cc);
    getlist(cc).then(({data:res})=>{
      if(res.code===20000){
        this.$store.commit('setmenu',res.data.menu)
      }else{
        this.$message.warning(res.data.message)
        console.log(res);
        }
    })
  },
   
  
}
</script>


<style lang='less' scope>
.el-header{
  background-color: #333;
}
.el-main{
  padding-top: 0;
}
</style>
