export default {
    getlist: (config) => {
        const { token } = JSON.parse(config.body)
        if (token === '778') {
            return {
                code: 20000,
                data: {
                    menu: [{
                            path: '/',
                            name: 'home',
                            label: '首页',
                            icon: 's-home',
                            url: 'home/home'
                        },
                        {
                            path: '/mall',
                            name: 'mall',
                            label: '商品管理',
                            icon: 'video-play',
                            url: 'MallMange/MallMange'
                        }, {
                            path: '/user',
                            name: 'user',
                            label: '用户管理',
                            icon: 'user',
                            url: 'UserMange/UserMange'
                        }, {
                            label: '其他',
                            icon: 'location',
                            children: [{
                                    path: '/page1',
                                    name: 'page1',
                                    label: '页面1',
                                    icon: 'setting',
                                    url: 'Other/PageOne'
                                },
                                {
                                    path: '/page2',
                                    name: 'page2',
                                    label: '页面2',
                                    icon: 'setting',
                                    url: 'Other/PageTwo'
                                },
                            ]
                        },
                    ],
                    message: '获取成功'

                },
            }
        } else if (token === '999') {
            return {
                code: 20000,
                data: {
                    menu: [{
                        path: '/',
                        name: 'home',
                        label: '首页',
                        icon: 's-home',
                        url: 'home/home'
                    }]
                },
                message: '获取成功'
            }

        } else {
            return {
                code: -99,
                data: {
                    message: '获取失败,无权限'
                }

            }
        }
    }
}