import Vue from 'vue'
import Vuex from 'vuex'
import Cookie from 'js-cookie'

Vue.use(Vuex)
const state = {
    isCollapse: false,
    islist: [],
    token: '',
    menu: []
}
const actions = {

}

const mutations = {
    qiehuan(state) {
        state.isCollapse = !state.isCollapse
    },
    mianbaoxie(state, val) {
        const result = state.islist.findIndex((item) => {
            return item.name === val.name
        })
        if (result === -1) {
            state.islist.push(val)
        }

    },
    closetag(state, val) {
        const result = state.islist.findIndex((item) => {
            return item.name === val.name
        })
        state.islist.splice(result, 1)

    },
    settoken(state, val) {
        state.token = val
        Cookie.set('token', val)
    },
    cleartoken(state) {
        state.token = ''
        Cookie.remove('token')
    },
    gettoken(state) {
        state.token = Cookie.get('token')
    },
    setmenu(state, val) {
        state.menu = val
        Cookie.set('menu', JSON.stringify(val))
    },
    clearmenu(state) {
        state.menu = []
        Cookie.remove('menu')
    },
    getmenu(state) {
        return state.menu = JSON.parse(Cookie.get('menu'))
    },
    addmenu(state, router) {
        const menu = state.menu
        const menuArray = []
        menu.forEach(item => {
            if (item.children) {
                item.children = item.children.map(item => {
                    item.component = () =>
                        import (`../view/${item.url}`)
                    return item
                })
                menuArray.push(...item.children)
            } else {
                item.component = () =>
                    import (`../view/${item.url}`)
                menuArray.push(item)
            }

        });
        menuArray.forEach(item => {
            router.addRoute('shouye', item)
        })
    }
}

export default new Vuex.Store({
    actions,
    mutations,
    state,
})