export default {
    getMenu: (config) => {
        const { username, password } = JSON.parse(config.body)
        if (username === 'admin' && password === 'admin') {
            return {
                code: 20000,
                data: {
                    menu: [{
                            path: '/',
                            name: 'home',
                            label: '首页',
                            icon: 's-home',
                            url: 'home/home.vue'
                        },
                        {
                            path: '/mall',
                            name: 'mall',
                            label: '商品管理',
                            icon: 'video-play',
                            url: 'mall/mall.vue'
                        }, {
                            path: '/user',
                            name: 'user',
                            label: '用户管理',
                            icon: 'user',
                            url: 'user/user.vue'
                        }, {
                            label: '其他',
                            icon: 'location',
                            children: [{
                                    path: '/page1',
                                    name: 'page1',
                                    label: '页面1',
                                    icon: 'setting',
                                    url: 'Other/Page1.vue'
                                },
                                {
                                    path: '/page2',
                                    name: 'page2',
                                    label: '页面2',
                                    icon: 'setting',
                                    url: 'Other/Page2.vue'
                                },
                            ]
                        },
                    ],
                    token: '778',
                    message: '获取成功'

                },
            }
        } else if (username === 'chen' && password === 'chen') {
            return {
                code: 20000,
                data: {
                    menu: [{
                        path: '/',
                        name: 'home',
                        label: '首页',
                        icon: 's-home',
                        url: 'home/home'
                    }],
                    token: '999',
                    message: '获取成功'
                }

            }

        } else {
            return {
                code: -99,
                data: {
                    message: '密码错误'
                }

            }
        }
    }
}