import axios from "./axios.js";

export const getmenu = (param) => {
    return axios.request({
        url: '/menu/getMenu',
        method: 'post',
        data: param
    })
}
export const getlist = (param) => {
    return axios.request({
        url: '/shauxin/getlist',
        method: 'post',
        data: param
    })
}