<template>
<div class="beijign">
    <el-form :model="form" status-icon :rules='rules' ref='form' label-width="100px" class="logins">
     <h3 class="login_title">系统登录</h3>
     <el-form-item label='用户' label-width="80" prop="username">
        <el-input type="input" v-model="form.username"  placeholder="请输入账号"></el-input>
     </el-form-item>
     <el-form-item label='密码' label-width="80" prop="password">
        <el-input type="password" v-model="form.password"  placeholder='请输入密码'></el-input>
     </el-form-item>
     <el-form-item>
        <el-button type="primary" @click="login()">登录</el-button>
     </el-form-item>
</el-form>
</div>
</template>

<script> 
import { getmenu } from'../../api/data'
export default {
    name:'login',
    data() {
        return {
            form:{
                username:'',
                password:''
            },
            rules:{
                username:[
                    {required:true,message:'请输入用户名',trigger:'blur'},
                    {min:3,message:'用户名长度不能小于三位',trigger:'blur'}
                ],
                password:[
                    {required:true,message:'请输入密码',trigger:'blur'},
                    {min:3,message:'密码长度不能小于三位',trigger:'blur'}
                ]
            }
        }
    },
    methods:{
        login(){
            getmenu(this.form).then(({data:res})=>{
                if(res.code===20000){
                    this.$store.commit('setmenu',res.data.menu)      
                    this.$store.commit('settoken',res.data.token)
                    this.$store.commit('addmenu',this.$router)
                    this.$router.push({
                       name:'shouye'
                            })
                } else{
                    this.$message.warning(res.data.message)
                }
            })

        }
    }
}
</script>

<style>
.logins{
    border-radius: 15px;
    background-clip: padding-box;
    width: 300px;
    border:1px solid #DCDFE6;
    margin:100px auto;
    padding: 10px 0 10px 20px;
}
.el-input{
    width: 200px;
}
.login_title{
    margin-left: 100px;
}
</style>