<template>
<el-menu default-active="1-4-1" class="el-menu-vertical-demo" background-color='#545c64' text-color='#fff' :active-text-color='color' @open="handleOpen" @close="handleClose" :collapse="isCollapse">
  <h3>{{isCollapse ?'后台':'通用后台管理系统'}}</h3>
  <el-menu-item @click="clickMenu(item)" v-for="item in nochildren" :index="item.path+''" :key="item.path">
    <i :class="'el-icon-'+item.icon"></i>
    <span slot="title">{{item.label}}</span>
  </el-menu-item>
  <el-submenu v-for="item in havechildren" :index="item.path+''" :key="item.path">
    <template slot="title">
      <i :class="'el-icon-'+item.icon"></i>
      <span slot="title">{{item.label}}</span>
    </template>
    <el-menu-item-group v-for="(citem,cindex) in item.children" :key="citem.path">
      <el-menu-item @click="clickMenu(citem)" :index="cindex+''">{{citem.label}}</el-menu-item>
    </el-menu-item-group>
  </el-submenu>

</el-menu>

</template>

<script>
export default {
    name:'ce',
    data() {
      return {
        menu:[],
        color:'#ffd04b'
      }
    },
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      clickMenu(item){
          this.$router.push({
              name:item.name
          })
          this.$store.commit('mianbaoxie',item)
      }
    },
    computed:{
        getmenu(){
          return this.$store.state.menu
        },
        nochildren(){
            return this.getmenu.filter((item) =>{
                return !item.children
            } )
        },
        havechildren(){
            return this.getmenu.filter((item) =>{
                return item.children
            } )
        },
        isCollapse(){
          return this.$store.state.isCollapse
        },

    }

}
</script>


    
<style lang='less' scoped>
 .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    height: 100vh;
  }
  .el-menu{
      height: 100%;
      border: none;
      h3{
          color: #fff;
          text-align:center;
          line-height:48px
      }
  }
</style>